/* Glass Effect */
.glass{
	position: relative;
	padding: 20px 50px;
	text-decoration: none;
	color: white;
	font-size: 2em;
	text-transform: uppercase;
	font-family: 'Courier New', Courier, monospace;
	letter-spacing: 4px;
	overflow: hidden;
	background: rgba(255,255,255,.1);
	box-shadow: 0 5px 5px rgba(0,0,0.2); 
}
.glass:before{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 57.6%;
	height: 100%;
	background: rgba(255,255,255,.1);
}
.glass:after{
	content: '';
	position: absolute;
	top: 0;
	left: -100%;
	width: 100%;
	height: 100%;
	background: linear-gradient(90deg,transparent,rgba(255,255,255,.4),transparent);
	transition: 0.5s;
	transition-delay: 0.5s;
} 
.glass:hover:after{
	left: 100%;
}
.glass span{
	position: absolute;
	display: block;
	transition: 0.5s ease;
}
.glass span:nth-child(1)
{
	top: 0;
	left: 0;
	width: 0;
	height: 1px;
	background: #fff;
}
.glass:hover span:nth-child(1)
{
	width: 100%;
	transform: translateX(100%);
}
.glass span:nth-child(3)
{
	bottom: 0;
	right: 0;
	width: 0;
	height: 1px;
	background: #fff;
}
.glass:hover span:nth-child(3)
{
	width: 100%;
	transform: translateX(-100%);
}
.glass span:nth-child(2)
{
	top: 0;
	left: 0;
	width: 1px;
	height: 0;
	background: #fff;
}
.glass:hover span:nth-child(2)
{
	height: 100%;
	transform: translateY(100%);
}
.glass span:nth-child(4)
{
	bottom: 0;
	right: 0;
	width: 1px;
	height: 0;
	background: #fff;
}
.glass:hover span:nth-child(4)
{
	height: 100%;
	transform: translateY(-100%);
}
